$breakpoints: (
  'small': 320px,
  'med': 620px,
  'large': 770px,
  'wide': 1400px,
  'mega': 2000px,
);

@mixin breakpoint($bp, $rule: min-width) {
  @media screen and (#{$rule}: map-get($breakpoints, $bp)) {
    @content;
  }
}

$primary-color: #c4c4c4;
$secondary-color: #2e2e2e;
$base-color: #aaa;
$border-medium: 3px;

.unselectable {
  pointer-events: none;
  user-select: none;
}
